import React, { Component } from 'react'

export default class Entry extends Component {

    state = {
        title :this.props.title,
        content: this.props.content
    }

    render() {
        return (
            <div className="post" style={styles.post}>
                {
                    this.props.images ? this.props.images.map(image => 
                        <img key={image.url} style={styles.image} src={image.url}></img>
                    ) :
                        null
                }
                <div style={styles.content}>
                    <ul style={styles.tags}>
                        {
                            this.props.tags.map(tag => 
                                <li style={styles.tag} key={tag + this.props.id} >{tag}</li>
                            )
                        }
                    </ul>
                    <h2>{this.state.title}</h2>
                    <p>{this.state.content}</p>
                </div>
            </div>
        )
    }
}

const styles = {
    tags: {
        display: 'flex',
        listStyle: 'none',
        justifyContent: 'center',
        marginLeft: '-50px'
    },

    tag: {
        padding: '5px 20px',
        margin: '0 10px',
        borderRadius: '3px',
        backgroundColor: '#F8054D',
        color: 'white'
    },

    post: {
        //backgroundColor: '#F8054D',
        backgroundColor: '#ececec',
        //border: '1px solid #cecece80',
        overflow: 'hidden',
        borderRadius: '5px',
        marginBottom: '2em',
    },
     
    image : {
        marginBottom: '-1em'
    },

    content: {
        padding: '3em'
    }

}